import { CreatePromiseData } from "./createPromiseData";

export async function importScript<T>(path: string, windowLibraryName: string, importField: string | "*" = "default") {
    const prom = CreatePromiseData<void>();
    const script = document.createElement("script");
    script.src = path;
    script.onload = () => prom.resolver();
    script.onerror = (...args) => prom.reject({ ...args });
    document.body.appendChild(script);
    await prom.promise;

    const module = (window as any)[windowLibraryName];
    try {
        delete (window as any)[windowLibraryName];
    } catch (error) { // throws "Cannot delete property of #<Window>", some declared properties are mounted on the window and are non-configurable.
    }

    return (importField == "*" ? module : module[importField]) as T;
}

export async function importModule<T>(path: RequestInfo, require: (name: string) => any, importField: string = "default") {
    const compjs = await importModuleFile(path);
    return importModuleBody<T>(compjs, require, importField);
}
export async function importModuleFile(path: RequestInfo) {
    const result = await fetch(path);
    return await result.text();
}
export async function importModuleBody<T>(moduleJs: string, require: (name: string) => any, importField: string | "*" = "default") {
    const module = {} as any, exports = {};
    new Function("module", "exports", "require", moduleJs)(module, exports, require);

    return (importField == "*" ? module.exports : module.exports[importField]) as T;
}