import { PlateauMessaging, PModules, FrameCommunicator, ISocketPackage } from "@stechquick/plateau-comm"
import { IWebSDK } from "../../../../common/shrimp/interfaces/quick/IWebSDK";
import { INamedCompModel } from "../../../../common/shrimp/interfaces/quick/INamedCompModel";
import { LazyLoader } from "../../../../common/shrimp/helpers/lazyLoader";
export class LivePreviewManager{
    private frameCommunication: FrameCommunicator<PModules>;
    private livePreviewID: string | undefined;
    private getQjsonUrlPrefix: string | undefined;
    constructor(targetWindow: Window) {
        this.frameCommunication = PlateauMessaging.CreatePlateauMessaging(targetWindow);
    }
    public async isItLivePreview():Promise<string | undefined> {
        var self = this;
        const response = await self.frameCommunication.Send("LivePreview", "getLPID", { type: "getLPID" }, { awaitResponse: true, awaitTime: 100 })?.catch((reason)=>{
            return;
        });
        
        if(!response || response.msg.type!="getLPIDResponse") {
            return;
        }
        self.livePreviewID=response.msg.ID;
        return response.msg.ID;       
    }
    public async getLivePreviewSettings():Promise<any | undefined> {
        var self = this;
        const response =
            await self.frameCommunication.Send("LivePreview", "getLPPlateauUIOptions", { type: "getLPPlateauUIOptions" }, { awaitResponse: true, awaitTime: 10000 })?.catch((reason) => {
                return;
            });
        if (!response || response.msg.type != "getLPPlateauUIOptionsResponse") {
            return;
        }
        self.getQjsonUrlPrefix = response.msg.getQJsonUrlPrefix;
        return response.msg.PlateauUIOptions;  
    }

    public setExternalQJsonRetriever(sdk: IWebSDK) {
        var self=this;
        if(!self.getQjsonUrlPrefix){
            return;
        }
        sdk.setQJsonUrlPrefix(self.getQjsonUrlPrefix);
    }

}