import { Hook } from "./hook";

type triggerCb = (...params: any) => void;
export class EventBus {
    private static AllEvents = "AllEvents";

    private subsDict: Record<string, Hook<triggerCb> | undefined>;
    constructor() {
        this.subsDict = {};
    }

    private obtainSubsList(eventName: string) {
        return this.subsDict[eventName] || (this.subsDict[eventName] = new Hook("EventBus_" + eventName));
    }

    public Subscribe(eventName: string, cb: triggerCb) {
        if (eventName == EventBus.AllEvents) { throw new Error(`${eventName} is a registered key. If you want to subscribe all events, use SubsribeAll().`); }

        this.obtainSubsList(eventName).subscribe(cb);
    }
    public Unsubscribe(eventName: string, cb: triggerCb) {
        if (eventName == EventBus.AllEvents) { throw new Error(`${eventName} is a registered key. If you want to subscribe all events, use SubsribeAll().`); }

        this.subsDict[eventName]?.unsubscribe(cb);
    }
    public SubscribeAll(cb: triggerCb) {
        this.obtainSubsList(EventBus.AllEvents).subscribe(cb);
    }

    public UnsubscribeAll(cb: triggerCb) {
        this.subsDict[EventBus.AllEvents]?.unsubscribe(cb);
    }
    public Trigger(eventName: string, ...params: any) {
        const allEventsHook = this.subsDict[EventBus.AllEvents];
        allEventsHook?.triggerer.apply(allEventsHook, params);

        if (eventName == EventBus.AllEvents) { return; }

        const hook = this.subsDict[eventName];
        hook?.triggerer.apply(hook, params);
    }
    public TriggerJustAllSubs(...params: any){
        const allEventsHook = this.subsDict[EventBus.AllEvents];
        allEventsHook?.triggerer.apply(allEventsHook, params);
    }

}