import { ISocket } from "./webSocket/iSocket";
import { ISocketPackage, StructuredMessageTypeDict, StructuredSocket } from "./webSocket/structuredSocket";

class WindowSocket implements ISocket {
    public OnMessageWithDirectObject = true;

    private targetWindow: Window;
    constructor(targetWindow: Window) {
        this.targetWindow = targetWindow;
        window.addEventListener("message", (ev) => {
            if (ev.source != targetWindow) {
                return;
            }
            this.OnMessage?.(ev.data);
        })
    }
    Send(packet: string | object): void {
        this.targetWindow.postMessage(packet, "*");
    }
    OnMessage?: ((data: any) => void) | undefined;
}


type MM<MesTypes extends Record<string, StructuredMessageTypeDict>, T extends keyof MesTypes> = keyof MesTypes[T];
export type MessageTypes<MesTypes extends Record<string, StructuredMessageTypeDict>, T extends keyof MesTypes> = MesTypes[T][MM<MesTypes, T>];
export class FrameCommunicator<MesTypes extends Record<string, StructuredMessageTypeDict>> {
    private structuredSocket: StructuredSocket<MesTypes[keyof MesTypes]>;
    private windowSocket: WindowSocket;
    constructor(targetWindow: Window) {
        this.windowSocket = new WindowSocket(targetWindow);
        this.structuredSocket = new StructuredSocket(this.windowSocket);
    }
    public Send<TModule extends keyof MesTypes, ReqType extends keyof MesTypes[TModule]>(target: TModule, messageType: ReqType, message: MesTypes[TModule][ReqType], options: {
        replyId?: string | undefined;
        awaitResponse?: boolean | undefined;
        awaitTime?: number | undefined;
    }) {
        const structuredSocket = (this.structuredSocket as unknown as StructuredSocket<MesTypes[TModule]>);
        return structuredSocket.Send(message, options);
    }

    public Subscribe<TModule extends keyof MesTypes, ReqType extends keyof MesTypes[TModule]>(target: TModule, messageType: ReqType, cb: (message: ISocketPackage<MesTypes[TModule][ReqType]>) => void) {
        const structuredSocket = (this.structuredSocket as unknown as StructuredSocket<MesTypes[TModule]>);
        structuredSocket.Subscribe(messageType, cb);
    }

    public SubscribeAll<Types>(cb: (message: ISocketPackage<Types>) => void) {
        this.structuredSocket.SubscribeAll(data => cb(data as unknown as ISocketPackage<Types>));
    }

    public UnsubscribeAll(cb: (message: ISocketPackage<MesTypes[string][string]>) => void) {
        this.structuredSocket.UnsubscribeAll(data => cb(data as ISocketPackage<MesTypes[string][string]>));
    }
}