import { CreatePromiseData, IPromiseData } from "../../interfaces/IPromiseData";
import { CryptoHelper } from "../cryptoHelper";
import { EventBus } from "../eventBus";
import { ISocket } from "./iSocket";





interface ISocketMessage {
    type: string;
    [key: string]: any;
}
export interface ISocketPackage<K> {
    id: string;
    replyId?: string;
    msg: K;
}



export type StructuredMessageTypeDict = Record<string, ISocketMessage>;

export class StructuredSocket<MessageType extends StructuredMessageTypeDict> {
    private socket: ISocket;
    private awaitMsgList: Record<string, { promData: IPromiseData<ISocketPackage<MessageType[keyof MessageType]>> } | undefined>;
    private eventBus: EventBus;
    constructor(socket: ISocket) {
        this.socket = socket;
        this.awaitMsgList = {};
        this.eventBus = new EventBus();
        socket.OnMessage = this.listen.bind(this);
    }

    public CreatePackage<K extends keyof MessageType>(msg: MessageType[K], options: { replyId?: string, awaitResponse?: boolean, awaitTime?: number }) {
        const packet: ISocketPackage<MessageType[K]> = { msg, id: CryptoHelper.CreateGuid(), replyId: options.replyId };
        return packet;
    }

    public Send<K extends keyof MessageType>(msg: MessageType[K], options: { replyId?: string, awaitResponse?: boolean, awaitTime?: number }) {
        const packet = this.CreatePackage(msg, options);
        this.socket.Send(packet);

        let promData: IPromiseData<ISocketPackage<MessageType[keyof MessageType]>> | undefined;
        if (options.awaitResponse) {
            promData = CreatePromiseData(options.awaitTime);
            this.awaitMsgList[packet.id] = { promData: promData };
        }

        return promData?.promise;
    }

    public Subscribe<K extends keyof MessageType>(key: K, cb: (data: ISocketPackage<MessageType[K]>) => void) {
        this.eventBus.Subscribe(key as string, cb);
    }

    public Unsubscribe<K extends keyof MessageType>(key: K, cb: (data: ISocketPackage<MessageType[K]>) => void){
        this.eventBus.Unsubscribe(key as string, cb);
    }

    public SubscribeAll(cb: (data: ISocketPackage<MessageType[keyof MessageType]>) => void){
        this.eventBus.SubscribeAll(cb);
    }
    public UnsubscribeAll(cb: (data: ISocketPackage<MessageType[keyof MessageType]>) => void){
        this.eventBus.UnsubscribeAll(cb);
    }


    public ConvertBufferToStructure(data: string | Buffer | ArrayBuffer | Buffer[] | object) {
        const packet = (this.socket.OnMessageWithDirectObject ? data : JSON.parse(data.toString())) as ISocketPackage<MessageType[keyof MessageType]>;
        if (!packet.msg || !packet.msg.type) { return undefined; }

        return packet;
    }

    private listen(data: string | Buffer | ArrayBuffer | Buffer[] | object) {
        let packet: ISocketPackage<MessageType[keyof MessageType]> | undefined;
        try {
            packet = this.ConvertBufferToStructure(data);
        } catch (ex) { }
        if (!packet) { return undefined; }

        if (packet.replyId) {
            const awaiter = this.awaitMsgList[packet.replyId];
            if (awaiter) {
                delete this.awaitMsgList[packet.replyId];
                awaiter.promData.resolver(packet);
            }
            this.eventBus.TriggerJustAllSubs(packet);
            return;
        }

        this.eventBus.Trigger(packet.msg.type, packet);
    }
}