import { IRenderer } from "../../common/shrimp/interfaces/RenderingInterfaces/IRenderer";
import { IWebSDK, IWebSDKSettings } from "../../common/shrimp/interfaces/quick/IWebSDK";
import { IContainerServices } from "../../common/shrimp/interfaces/quick/IContainerServices";
import { IDomElement } from "../../common/shrimp/interfaces/RenderingInterfaces/IDomElement";
import { Legacy } from "./build/legacy";
import { importScript, importModule, importModuleBody } from "@stechquick/algae/lib/helpers/importHelper";
import { IRendererConfig } from "../../common/shrimp/interfaces/RenderingInterfaces/IRendererConfig";
import { IQJSon } from "../../common/shrimp/interfaces/ComponentInterfaces/IQJson";
import { QJsonExtractHelper } from "../../common/shrimp/helpers/qjsonParser/QJsonExtractHelper";
import { LazyLoader } from "../../common/shrimp/helpers/lazyLoader";
import { LivePreviewManager } from "./src/managers/LivePreviewManager";
import { IRequestData, IPlateauUIObject, IPlateauUIOptions, contentFormat, contentType, ISettingsIAM, ISettings } from "@stechquick/algae/lib/quick/IPlateauUI"

class PlateauUI {
    private container: WebsdkContainer;
    private renderer: IRenderer;
    public mountPoint: HTMLElement;

    constructor(container: WebsdkContainer, renderer: IRenderer, mountPoint: IDomElement) {
        this.container = container;
        this.renderer = renderer;
        this.mountPoint = mountPoint;
    }
    open(qjsonPath?: string, args?: Record<string, any>) {
        this.container.open({ renderer: this.renderer, qjsonPath, args });
    }
    show() {
        this.mountPoint.style.display = "block";
    }
    hide() {
        this.mountPoint.style.display = "none";
    }
    close() {
        this.renderer.Hibernate();
    }
    trigger({ eventName, parameters }: { eventName: string, parameters: Record<string, any> }) {
        return this.renderer.Trigger(eventName, parameters);
    }
}

class WebsdkContainer {
    private enviromentInfo: string = "sdk";
    private settingsWeb?: ISettings; // combined and unprocessed form of settings for web. 

    private SDKInstance?: IWebSDK;
    private SDKSettings?: IWebSDKSettings; // settingsWeb["settings"]
    private SDKConfiguration?: IRendererConfig;
    //LIMBO: webSDKSettings & webSDKConfiguration, if they could exist while in the same object..


    public mountPoint?: IDomElement;
    private PlateauIAM: any = undefined;
    private PlateauUIOptions: IPlateauUIOptions = {
        corsEnabled: true,
        iam: "./settings/settings_iam.js",
        settings: "./settings/settings_web.js"
    }
    lpManager?: LivePreviewManager;

    constructor() { }

    public async init({ PlateauUIOptions, environmentInfo = "sdk" }: { PlateauUIOptions?: IPlateauUIOptions, environmentInfo?: string }) {
        const self = this;
        self.enviromentInfo = environmentInfo;
        var LPPlateauUIOptions = await self.configureLivePreview();
        self.PlateauUIOptions = LPPlateauUIOptions ? LPPlateauUIOptions : (PlateauUIOptions ?? this.PlateauUIOptions);




        if (!self.settingsWeb) {
            if(self.lpManager){
                self.settingsWeb = self.PlateauUIOptions.settings as ISettings;

                self.settingsWeb.settings = self.settingsWeb.settings || { value: {}, type: "object" };

                if (self.settingsWeb.localProxy && self.settingsWeb.localProxy.type == "object") {
                    self.settingsWeb.settings.value.settingsProxyYaml = self.settingsWeb.settings.value.settingsProxyYaml || {};
                    self.settingsWeb.settings.value.settingsProxyYaml["localProxy"] = self.settingsWeb.localProxy.value;
                }
            }
            else {
                var settingsWeb: ISettings = await self.LoadJS<any>(self.PlateauUIOptions.settings as string, "plateauUISettings", "*");
                if (!settingsWeb) {
                    throw "please set following: PlateauUIOptions['settings']"
                }
                self.settingsWeb = settingsWeb;
            }
            self.SDKSettings = Legacy.migrateLegacySettings(self.settingsWeb.settings.value);
        }


        if (self.PlateauUIOptions.iam) {
            var settingsIAM: ISettingsIAM | undefined = await self.LoadJS<ISettingsIAM>(self.PlateauUIOptions.iam, "plateauUIIAMOptions", "*", false);
            if (settingsIAM && settingsIAM.iam?.active) {
                self.PlateauIAM = new (await import("./src/managers/PlateauIAM")).PlateauIAM();
                self.PlateauIAM.setOptions(settingsIAM.iam.options)
                self.PlateauIAM.settingsIAM = settingsIAM
                // rootqjson kontrolu
                let securePage = true
                let unsecurePages = self.PlateauIAM.settingsIAM?.iam?.unsecurePages
                if(unsecurePages) {
                    for(let i=0;i<unsecurePages.length;i++) {

                        let queryString = Legacy.fixQueryString();
                        
                        if(unsecurePages[i] === queryString)//self.settingsWeb.settings.value.rootqjson)
                            securePage = false
                    }
                }

                if(securePage) {
                    await self.PlateauIAM.login();
                    if (!self.PlateauIAM.isAuthenticated()) {
                        throw Error("Authentication: Must logon");
                    }    
                }

            }

        }

        

        if (!self.SDKInstance) {
            var containerServices: any
            if (self.settingsWeb?.containerServices) {
                containerServices = await self.LoadObject<IContainerServices>(self.settingsWeb.containerServices, "plateauUIContainerServices", "*", false);
                containerServices = Legacy.containerServicesModifications(self, containerServices);
            }
            self.SDKInstance = new (await import("./src/WebSDK")).WebSDK(self.SDKSettings!, containerServices, { environmentName: environmentInfo });
            if (self.lpManager) {
                self.lpManager.setExternalQJsonRetriever(self.SDKInstance!);
            }
            if (window.location.search) document.cookie = "queryParams=" + window.location.search + ";path=/;"
        }

        const rendererConfig = await self.setSDKConfig();
        self.SDKConfiguration = rendererConfig;

    }

    public async createRenderer({ mountPoint }: { mountPoint: HTMLElement }) {
        const self = this;
        const renderer = self.SDKInstance!.CreateRenderer({ mountPoint, config: self.SDKConfiguration });
        await self.setSettingsQJsons({ renderer });
        var plateauUI = new PlateauUI(self, renderer, mountPoint);
        return plateauUI;
    }


    public async open({ renderer, qjsonPath, args }: { renderer: IRenderer, qjsonPath?: string, args?: Record<string, any> }) {
        const self = this;
        //qui -> q=? (queryString) , qjsonPath=? (queryString) , rootQjson (settings.yaml), 
        //sdk -> qjsonPath (parameter), rootQjson (settings.yaml) 
        let renderOptions;
        let queryString = Legacy.fixQueryString();
        if (this.enviromentInfo == "sdk" && qjsonPath) {
            qjsonPath = Legacy.fixLegacyPathFormat(qjsonPath);
            renderOptions={ pjsonPath: qjsonPath, storeItems: args };
        }
        else if (this.enviromentInfo == "qui" && queryString) {
            renderOptions = { pjsonPath: queryString, storeItems: args };
        }
        else if (!renderOptions && self.settingsWeb?.rootqjson) {
            var qjsonContent = await self.LoadObject<any>(self.settingsWeb?.rootqjson, "");
            const pageName = self.settingsWeb.settings.value.rootqjson;
            renderOptions={ pjsonContent: qjsonContent, pageName, storeItems: args };
        } else {
            throw Error("please set following: PlateauUIOptions.rootqjson")
        }
        renderer.Render(renderOptions);
        const ux = this.SDKInstance?.getUXManager();
        ux?.setTheme("default");
    }

    private async setSDKConfig() {
        var self = this;
        var plateauUIComponentOptions: any;
        if (self.settingsWeb?.componentList) {
            plateauUIComponentOptions = self.LoadObject<any>(self.settingsWeb.componentList, "plateauUIComponentOptions", "*", false);
        }
        var plateauUIGlobalLRQjson: any;
        if (self.settingsWeb?.globalLocalization) {
            plateauUIGlobalLRQjson = self.LoadObject<any>(self.settingsWeb?.globalLocalization, "", "*", false); //windowLibraryName
            self.LoadQjson(self.settingsWeb?.globalLocalization);
        }

        var rendererConfig: IRendererConfig = {};
        return Promise.all([plateauUIComponentOptions, plateauUIGlobalLRQjson]).then(([plateauUIComponentOptions, plateauUIGlobalLRQjson]) => {
            if (plateauUIComponentOptions) {
                rendererConfig.SiteSettings = { compSettings: plateauUIComponentOptions }
            }
            if (plateauUIGlobalLRQjson) {
                rendererConfig.GlobalLRDict = plateauUIGlobalLRQjson.clrids;
            }
            if (self.SDKSettings?.rootLangCode) {
                rendererConfig.LRType = self.SDKSettings?.rootLangCode;
            }
            return rendererConfig;
        });
    }

    private async setSettingsQJsons({ renderer }: { renderer: IRenderer }) {
        var self = this;
        if (self.settingsWeb?.pipeline) {
            const qjson = await self.LoadObject<any>(self.settingsWeb?.pipeline, "", "*", false); //windowLibraryName
            if (qjson) {
                try {
                    await renderer.settingsQJsons.SetPipelineQjson(qjson);
                } catch (err) {
                }
            }
        }
        if (self.settingsWeb?.alert) {
            const qjson = await self.LoadObject<any>(self.settingsWeb?.alert, "", "*", false); //windowLibraryName ?
            if (qjson) {
                try {
                    await renderer.settingsQJsons.SetAlertQjson(qjson);
                } catch (err) {
                }
            }
        }
        if (self.settingsWeb?.loading) {
            const qjson = await self.LoadObject<any>(self.settingsWeb?.loading, "", "*", false); //windowLibraryName
            if (qjson) {
                try {
                    await renderer.settingsQJsons.SetLoadingQjson(qjson);
                } catch (err) {
                }
            }
        }

        if (self.SDKSettings?.domainModuleList) {
            await self.SDKInstance?.LoadModuleCompLists(self.SDKSettings.domainModuleList).catch(ev => {
                console.log(ev);
            });
        }
    }

    private async LoadObject<T>(data: IPlateauUIObject, windowLibraryName: string, importField?: string | "*", required = true) {
        const self = this;
        if(!data){
            return;
        }
        if (data.format == "qjson") {
            return await self.LoadQjson(data);
        }
        if (data.format == "lottie") {
            throw new Error("Not implemented");
        }
        if (data.type == "path") {
            return await self.LoadJS(data.value, windowLibraryName, importField, required)
        }
        if (data.type == "object") {
            return data.value;
        }
        if (data.type == "js") {
            return await importModuleBody<T>(data.value, () => { }, importField);
        }
    }

    private async LoadJS<T>(requestData: string | IRequestData, windowLibraryName: string, importField?: string | "*", required = true) {
        const self = this;
        try {
            if (this.PlateauUIOptions.corsEnabled && typeof requestData == "string") {
                return await importScript<T>(requestData, windowLibraryName, importField);
            }
            else {
                let requestObject = typeof requestData == "string" ? requestData : new Request(requestData.url, { method: 'GET', body: JSON.stringify(requestData.body), headers: requestData.headers });
                return await importModule<T>(requestObject, () => { }, importField);
            }
        } catch (error) {
            if (!required) {
                return undefined;
            }
            throw new Error("couldn't load " + JSON.stringify(requestData));
        }

    }

    private async LoadQjson(plateauUIObject: { value: any, type: contentType }) {
        let qJsonString = "";
        if (plateauUIObject.type == "path") {
            var qjsonPath = Legacy.fixLegacyPathFormat(plateauUIObject.value)
            qJsonString = (await LazyLoader.FetchModuleBody(qjsonPath)).body; //TODO:newWebSDKBuild change to load from importHelper
        }
        if (plateauUIObject.type == "js") {
            qJsonString = plateauUIObject.value;
        }
        if (!qJsonString) {
            return undefined;
        }
        const qJsonExtracted = <IQJSon>QJsonExtractHelper.qJsonExtractor(qJsonString);
        if (!qJsonExtracted) {
            throw new Error("qJsonExtracted is undefined");
        }
        return qJsonExtracted;
    }
    private async configureLivePreview() {
        var self = this;
        var lpManager = new LivePreviewManager(parent);

        var resp = await lpManager.isItLivePreview();
        if (!resp) { return; }
        self.lpManager = lpManager;
        var settingsResponse = await lpManager.getLivePreviewSettings();
        if (!settingsResponse) { return; }
        return settingsResponse;
    }
}

var websdkContainerInstance: WebsdkContainer | undefined;

async function init(PlateauUIOptions?: IPlateauUIOptions, environmentInfo?: string) {
    if (websdkContainerInstance) { return; }
    websdkContainerInstance = new WebsdkContainer();
    await websdkContainerInstance.init({ PlateauUIOptions, environmentInfo });
    return websdkContainerInstance;
}

function createRenderer({ mountPoint }: { mountPoint: IDomElement }) {
    if (!websdkContainerInstance) {
        throw "websdk initiation went wrong."
    }
    const plateauUI = websdkContainerInstance.createRenderer({ mountPoint });
    return plateauUI;

}

export { init, createRenderer };